<template>
  <div>
    <!-- 顶部四大数据总览 -->
    <el-row :gutter="20" class="mgb20">
      <el-col :span="6">
        <el-card shadow="hover" class="grid-content-center">
          <div slot="header" class="grid-num">
            <span>{{ consumerCount }}</span>
          </div>
          用户总数
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover" class="grid-content-center">
          <div slot="header" class="grid-num">
            <span>{{ songCount }}</span>
          </div>
          歌曲总数
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover" class="grid-content-center">
          <div slot="header" class="grid-num">
            <span>{{ singerCount }}</span>
          </div>
          歌手数量
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover" class="grid-content-center">
          <div slot="header" class="grid-num">
            <span>{{ songListCount }}</span>
          </div>
          歌单数量
        </el-card>
      </el-col>
    </el-row>
    <!-- 图形化数据展示 -->
    <el-row :gutter="20" class="mgb20">
      <el-col :span="12">
        <h3 class="mgb20">用户性别比例</h3>
        <div style="background-color: white;">
          <ve-pie :data="consumerSex" :theme="consumerOptions"></ve-pie>
        </div>
      </el-col>
      <el-col :span="12">
        <h3 class="mgb20">歌单类型分布</h3>
        <div style="background-color: white;">
          <ve-histogram :data="songStyle"></ve-histogram>
        </div>
      </el-col>
    </el-row>
    <!-- 图形化数据展示 -->
    <el-row :gutter="20" class="mgb20">
      <el-col :span="12">
        <h3 class="mgb20">歌手性别比例</h3>
        <div style="background-color: white;">
          <ve-pie :data="singerSex" :theme="singerOptions"></ve-pie>
        </div>
      </el-col>
      <el-col :span="12">
        <h3 class="mgb20">歌单地区分布</h3>
        <div style="background-color: white;">
          <ve-histogram
            :data="singerLocation"
            :theme="locationOptions"
          ></ve-histogram>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import {
  getAllConsumer,
  allSong,
  getAllSinger,
  getAllSongList
} from "../api/index";
export default {
  data() {
    return {
      consumerCount: 0, //用户数量
      songCount: 0, //歌曲数量
      singerCount: 0, //歌手数量
      songListCount: 0, //歌单数量
      consumer: [],
      consumerSex: {
        columns: ["性别", "总数"],
        rows: [
          { 性别: "男", 总数: 0 },
          { 性别: "女", 总数: 0 }
        ]
      },
      consumerOptions: {
        color: ["#87cefa", "#ffc0cb"]
      },
      singerOptions: {
        color: ["#87cefa", "#ffc0cb", "#9e52e5", "#808080"]
      },
      locationOptions: {
        color: ["#47cd81", "#578dd9", "#cb3131", "#e6f16e"]
      },
      songStyle: {
        columns: ["风格", "总数"],
        rows: [
          { 风格: "欧美", 总数: 0 },
          { 风格: "日系", 总数: 0 },
          { 风格: "华语", 总数: 0 },
          { 风格: "摇滚", 总数: 0 },
          { 风格: "金属", 总数: 0 }
        ]
      },
      singerSex: {
        columns: ["性别", "总数"],
        rows: [
          { 性别: "男", code: 0, 总数: 0 },
          { 性别: "女", code: 1, 总数: 0 },
          { 性别: "组合", code: 2, 总数: 0 },
          { 性别: "不明", code: 3, 总数: 0 }
        ]
      },
      singerLocation: {
        columns: ["地区", "总数"],
        rows: [
          { 地区: "中国", 总数: 0 },
          { 地区: "日本", 总数: 0 },
          { 地区: "理塘", 总数: 0 },
          { 地区: "美国", 总数: 0 },
          { 地区: "其他", 总数: 0 }
        ]
      }
    };
  },
  created() {},
  mounted() {
    this.getConsumer();
    this.getSong();
    this.getSinger();
    this.getSongList();
  },
  methods: {
    // 用户总数
    getConsumer() {
      getAllConsumer().then(res => {
        this.consumer = res;
        this.consumerCount = res.length;
        this.consumerSex.rows[0]["总数"] = this.getByConsumerSex(
          1,
          this.consumer
        );
        this.consumerSex.rows[1]["总数"] = this.getByConsumerSex(
          0,
          this.consumer
        );
      });
    },
    //根据性别获取用户数量
    getByConsumerSex(sex, consumer) {
      let count = 0;
      for (let item of consumer) {
        if (sex === item.sex) {
          count++;
        }
      }
      return count;
    },
    // 歌曲总数
    getSong() {
      allSong().then(res => {
        this.songCount = res.length;
      });
    },

    // 歌手总数
    getSinger() {
      getAllSinger().then(res => {
        this.singerCount = res.length;

        for (let item of res) {
          console.log(item.sex);
          this.getBySingerSex(item.sex);
          this.getBySingerLocation(item.location);
        }
      });
    },
    //根据性别分类的歌手数量
    getBySingerSex(sex) {
      for (let item of this.singerSex.rows)
        if (sex === item["code"]) {
          item["总数"]++;
        }
    },
    //根据歌手地区分类的歌手数量
    getBySingerLocation(location) {
      for (let item of this.singerLocation.rows)
        if (location.includes(item["地区"])) {
          item["总数"]++;
        }
    },
    // 歌单总数
    getSongList() {
      getAllSongList().then(res => {
        this.songListCount = res.length;

        for (let item of res) {
          this.getByStyle(item.style);
        }
      });
    },
    //根据歌单风格获取歌曲数量
    getByStyle(style) {
      for (let item of this.songStyle.rows)
        if (style.includes(item["风格"])) {
          item["总数"]++;
        }
    }
  }
};
</script>
<style>
/* .grid-content {
  padding: 20px;
  display: flex;
  align-items: center;
} */
.grid-content-center {
  flex: 1;
  text-align: center;
  font-size: 14px;
  border-radius: 10px;
}
.grid-num {
  font-size: 30px;
  font-weight: bold;
}
</style>



